import React from "react";
import { Link } from "react-router-dom";
import { alertBox } from "../../commonRedux/";
import A from "../../components/A";
import Button from "../../components/Button";
import GigFilter from "../../components/Filter/gigFilter";
import CreateGigWidget from "../../components/Gigs/Create";
import Gig from "../../components/Gigs/Gig";
import GigRequest from "../../components/Gigs/GigRequest";
import PopularGigs from "../../components/Gigs/PopularGigs";
import Modal from "../../components/Popup";
import TabsUI from "../../components/Tabs/index";
import { allGigRequests, getGigs } from "../../http/gig-calls";
import { postReport, postUnReport } from "../../http/http-calls";
import Images from "../../assets/images/images";
import AdminActivity from "./adminActivity/index.js";
import TopicsForPost from "./topicsForPost";
import ScheduledPosts from "./scheduledPosts/index";
import MemberRequest from "./memberRequest/index";
import Notification from "./notification/index";
import CreateRules from "./createRules";
import MembershipQuestions from "./membershipQuestions";
import MemberReported from "./memberReported";
import GroupPurchasedPlan from "./groupPurchasedPlan";
import "./gigs.css";
import Header from "../../components/Header";
import SocialActivities from "../../components/Menu/SocialActivities";
import images from "../../assets/images/images";

class ViewUserGroupActivity extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      limit: 4,
      page: 1,
      cid: 0,
      posts: [],
      category: "",
      reason: "",
      reportModal: false,
      data: {
        category: "",
        subCategory: "",
        priceFrom: "",
        priceTo: "",
        rating: "",
        key: "",
      },
      lastData: null,
      lastI: null,
      lastType: null,
      gigs: [],
      currentTab: 1,
    };
  }

  reportModal = (p = null, i = null, t = null) => {
    this.setState({
      lastData: p,
      lastI: i,
      lastType: t,
    });
    this.setState({ reportModal: !this.state.reportModal }); // true/false toggle
  };

  report = () => {
    var lastData = this.state.lastData;
    var lastI = this.state.lastI;
    var link = "gigonomy/gig/" + lastData._id;
    postReport({
      id: lastData._id,
      type: this.state.lastType,
      link,
      category: this.state.category,
      reason: this.state.reason,
    }).then(
      async (resp) => {
        alertBox(true, resp.message, "success");
        var temp = this.state.posts;
        temp[lastI].reported = 1;
        this.setState({ reportModal: false, posts: temp });
      },
      (error) => {
        alertBox(true, error.data.message);
        this.setState({ reportModal: false });
      }
    );
  };
  unReport = (post, i) => {
    postUnReport({ id: post._id }).then(
      async (resp) => {
        alertBox(true, resp.message, "success");
        var temp = this.state.posts;
        temp[i].reported = 0;
        this.setState({ reportModal: false, posts: temp });
      },
      (error) => {
        alertBox(true, error.data.message);
      }
    );
  };

  componentDidMount = () => {
    // this.getData();
    // this.getRequests();
  };

  categoryChange = (c) => {
    this.setState({
      cid: c,
      currentTab: 0,
    });
  };

  dataChange = (data = {}) => {
    //this.props.history.push("/passionomy/gig");
    this.getData(data);
    this.getRequests(data);
  };

  getData = (data = {}) => {
    this.setState({
      gigs: [],
    });
    var d = data;
    console.log(d);
    d["page"] = this.state.page;
    d["limit"] = this.state.limit;
    d["userid"] = this.props.currentUser._id;
    getGigs(d).then(
      (resp) => {
        this.setState(
          {
            gigs: resp.post,
          },
          () => {
            console.log(this.state.gigs);
          }
        );
      },
      (err) => {
        console.log(err);
      }
    );
  };

  getRequests = (data = {}) => {
    this.setState({
      posts: [],
    });
    var d = data;
    d["page"] = this.state.page;
    d["limit"] = this.state.limit;
    d["userid"] = this.props.currentUser._id;
    allGigRequests(data).then(
      (resp) => {
        this.setState({
          posts: resp,
        });
      },
      (err) => {}
    );
  };
  changeTab = (newValue) => {
    this.setState({ currentTab: newValue });
  };

  render() {
    const { currentTab: tabValue } = { ...this.state };
    return (
      <div className="viewEditGroupModerator">
        <Header
          appName={this.props.appName}
          currentUser={this.props.currentUser}
        />
        <div className="container my-wall-container ">
          <div className="row mt-2">
            {/* <!-- left column --> */}
            <div className="col-sm empty-container-with-out-border left-column">
              <SocialActivities />
              <div className="groupAreaWrapper">
                <div className="group">
                  <input className="file" id="ProfileImgFile" type="file" />
                  <div className="groupImg">
                    <img src={images.profileM} alt="img" />
                    <div className="changeImg">
                      <label style={{ cursor: "pointer" }} for="ProfileImgFile">
                        <span>Change Image</span>
                      </label>
                    </div>
                  </div>
                  <span className="groupName">
                    Group Name of user will be shown here
                  </span>
                  <div className="groupAccessType">
                    <img src={images.locked} alt="locked" />
                    <span>Private Group</span>
                  </div>
                  <span className="groupDescription">
                    Developing a solid brand makes selling easier. Brand
                    Strategy and Design communicates your ease so that you can
                    rise above the competition.
                  </span>
                  <div className="float-right">
                    <Link className="float-right">
                      See More <i className="fa fa-caret-down"></i>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            {/* <!-- end left column --> */}

            {/* <!-- center column --> */}
            <div className="col-sm empty-container-with-out-border center-column">
              <div className="groupSettingsArea">
                {tabValue == 1 && <AdminActivity />}
                {tabValue === 2 && <TopicsForPost />}
                {tabValue === 3 && <ScheduledPosts />}
                {tabValue === 4 && <MemberRequest />}
                {tabValue === 5 && <Notification />}
                {tabValue === 6 && <CreateRules />}
                {tabValue === 7 && <MembershipQuestions />}
                {tabValue === 8 && <MemberReported />}
                {tabValue === 9 && <GroupPurchasedPlan />}
              </div>
            </div>
            {/* <!-- end center column --> */}

            {/* <!--  right column --> */}
            <div className="col-sm empty-container-with-out-border right-column">
              <div className="ListGroup">
                <ul className="list-group">
                  <li
                    onClick={() => this.changeTab(1)}
                    className="list-group-item d-flex active justify-content-between align-items-center"
                  >
                    Admin Activity
                  </li>
                  <li
                    onClick={() => this.changeTab(2)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Topics For Posts
                  </li>
                  <li
                    onClick={() => this.changeTab(3)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Scheduled Posts
                    <span className="badge badge-primary badge-pill">2</span>
                  </li>
                  <li
                    onClick={() => this.changeTab(4)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Member Requests
                    <span className="badge badge-primary badge-pill">1</span>
                  </li>
                  <li
                    onClick={() => this.changeTab(5)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Approval Notifications
                  </li>
                  <li
                    onClick={() => this.changeTab(6)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Create Rules
                  </li>
                  <li
                    onClick={() => this.changeTab(7)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Membership Questions
                  </li>
                  <li
                    onClick={() => this.changeTab(8)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Member Reported
                  </li>
                  <li
                    onClick={() => this.changeTab(9)}
                    className="list-group-item d-flex justify-content-between align-items-center"
                  >
                    Group Purchased Plan
                  </li>
                </ul>
              </div>
              {/* <CreateGigWidget extra={true} />
                    <PopularGigs /> */}
            </div>
            {/* <!-- end right column --> */}
          </div>
        </div>
      </div>
    );
  }
}

export default ViewUserGroupActivity;
